<template>
    <div class="setusername">
        <div class="rating_page">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>修改用户名</span>
        </header>
            <div class="setname">
                <div class="setname_top" v-show="isShow1">
                    <input type="text" class="setname_top_input" v-model="inputV">
                    <div>
                        <p class="setname_top_div_p">用户名只能修改一次（5-24字符之间）</p>
                    </div>
                </div>
                <div class="setname_top" v-show="isShow2">
                    <input type="text" class="setname_top_input1" v-model="inputV">
                    <div>
                        <p class="setname_top_div_p1">用户名长度5到24位之间</p>
                    </div>
                </div>
                <div class="reset">
                    <button class="reset_btn" @click="setusername">确认修改</button>
                </div>
            </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "SetUserName",
        data(){
            return {
                inputV:"",
                user:{},
                isShow1:true,
                isShow2:false,
            }
        },
        created(){
            this.user = JSON.parse(localStorage.getItem('profile'));
        },
        methods:{
            setusername(){
                if (this.inputV.length < 5) {
                    this.isShow1 = false;
                    this.isShow2 = true
                }else {
                    this.user.username = this.inputV;
                    this.$router.push("/info");
                    localStorage.setItem("profile",JSON.stringify(this.user));
                }
            }
        }
    }
</script>

<style scoped>
    .setname_top_input1{
        background: none;
        width: 15.2rem;
        border: 1px solid red;
        border-radius: 2px;
        padding: .2rem .1rem;
        line-height: 1.2rem;
        font-size: .7rem;
        display: block;
    }
    .setname_top_div_p1{
        width: 100%;
        font-size: .58rem;
        color: red;
        padding: .4rem 0 1rem;
        padding-top: .25rem;
        font-family: Helvetica Neue,Tahoma,Arial;
    }

    .setusername{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .rating_page{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 202;
        background: #f2f2f2;
        padding-top: 1.95rem;
    }
    .setname{
        width: 15.2rem;
        margin: 0 auto;
    }
    .setname_top{
        padding-top: .4rem;
    }
    .setname_top_input{
        background: none;
        width: 15.2rem;
        border: 1px solid #ddd;
        border-radius: 2px;
        padding: .2rem .1rem;
        line-height: 1.2rem;
        font-size: .7rem;
        display: block;
        outline:none;
    }
    .setname_top_div_p{
        width: 100%;
        font-size: .4rem;
        color: #666;
        padding: .4rem 0 1rem;
    }
    .reset{
        width: 100%;
        background: #3199e8;
    }
    .reset_btn{
        display: block;
        width: 100%;
        background: none;
        line-height: 2rem;
        font-size: .7rem;
        color: #fff;
        transition: all 1s;
        outline:none;
    }
</style>